
<template>
  <div class="upload-file">
    <!-- :headers="headers" -->
      <!-- :data="{ 上传时附带的额外参数 }" -->
    <el-upload
      multiple
      name="multipartFile"
      :action="uploadFileUrl"
      :http-request="uploadRequest"
      :limit="limit"
      :file-list="fileList"
      :before-upload="handleBeforeUpload"
      :on-exceed="handleExceed"
      :on-error="handleUploadError"
      :on-success="handleUploadSuccess"
      :show-file-list="true"
      
      class="upload-file-uploader"
      ref="fileUpload"
    >
      <!-- 上传按钮 v-if="!disabled" -->
      <el-button size="mini" type="primary" plain >选取文件</el-button> 
      <!-- 上传提示 -->
      <div class="el-upload__tip" slot="tip" v-if="showTip && !disabled">
        请上传
        <template v-if="fileSize">
          大小不超过 <b style="color: #f56c6c">{{ fileSize }}MB</b>
        </template>
        <template v-if="fileType">
          格式为 pdf
        </template>
        的文件
      </div>
    </el-upload>

  </div>
</template>

<script>
// import { 查看和下载的接口，后端给的 } from "";


//引入VueOfficePdf组件，需要npm安装
import axios from 'axios';

axios.defaults.withCredentials = true;

export default {
  name: "FileUpload",
  props: {
    // 数量限制
    limit: {
      type: Number,
      default: 5,
    },
    // 大小限制(MB)
    fileSize: {
      type: Number,
      default: 20,
    },
    // 文件类型, 例如['png', 'jpg', 'jpeg']
    fileType: {
      type: Array,
      default: () => ["docx", "pptx", "pdf"],
    },
    // 是否显示提示
    isShowTip: {
      type: Boolean,
      default: true,
    },
    formFileList: {
      type: Array,
      default: () => [],
    },
    disabled: {
      type: Boolean,
      default: false,
    },
    // 培训记录附件的类型
    busiType: {
      type: String,
    }

  },
  data() {
    return {
      number: 0,
      uploadList: [],
      baseUrl: "http://118.31.113.55:8083" ,  // 地址
      uploadFileUrl:"http://118.31.113.55:8083/updateFiles",// 上传文件服务器地址
      headers: {
        cookies: document.cookie , //+ getToken()
      },
      fileList: [],
      lookFile: false,
      url: "",
      // 文件预览
      preview: {
        open: false,
        url: "",
      },
      fileSuffix: "",
      loadindgInstance: null,
    };
  },
  computed: {
    // 是否显示提示
    showTip() {
      return this.isShowTip && (this.fileType || this.fileSize);
    },
  },
  methods: {
    // 上传前校检格式和大小
    handleBeforeUpload(file) {
      // 校检文件类型
      if (this.fileType) {
        const fileName = file.name.split(".");
        const fileExt = fileName[fileName.length - 1];
        console.log(fileExt)
        if (fileExt !== "pdf") {
          alert(`文件格式不正确, 请上传${this.fileType.join("/")}格式文件!`);
          return false;
        }
      }
      // 校检文件大小
      if (this.fileSize) {
        const isLt = file.size / 1024 / 1024 < this.fileSize;
        if (!isLt) {
          alert(`上传文件大小不能超过 ${this.fileSize} MB!`);
          return false;
        }
      }
      this.loadindgInstance = this.$loading("正在上传文件，请稍候...");
      console.log("is loading------")
      this.number++;
      return true;
    },
    // 文件个数超出
    handleExceed() {
      alert(`上传文件数量不能超过 ${this.limit} 个!`);
    },
    // 上传失败
    handleUploadError(err) {
      this.number--;
      alert("上传文件失败，请重试");
      this.loadindgInstance.close();
      console.log(err)
    },
    // 上传成功回调
    handleUploadSuccess(res, file) {
      if (res.status === 200) {
        this.uploadList.push(file.name);
        this.loadindgInstance.close()
        this.uploadedSuccessfully();
      } else {
        this.number--;
        this.loadindgInstance.close()
        alert(res.msg);
        this.$refs.fileUpload.handleRemove(file);
        this.uploadedSuccessfully();
      }
    },
    // 删除文件
    handleDelete(index) {
      this.fileList.splice(index, 1);
    },
    // 上传结束处理
    uploadedSuccessfully() {
      if (this.number > 0 && this.uploadList.length === this.number) {
        this.fileList = this.fileList.concat(this.uploadList);
        this.uploadList = [];
        this.number = 0;
        // this.$emit("input", this.listToString(this.fileList));
        this.loadindgInstance.close()
        // this.$emit("fileUploadSuccess", this.fileList);
      }
    },
    // 获取文件名称
    // 对象转成指定字符串分隔
    listToString(list, separator) {
      let strs = "";
      separator = separator || ",";
      for (let i in list) {
        strs += list[i].url + separator;
      }
      return strs != "" ? strs.substr(0, strs.length - 1) : "";
    },
    resetFileList() {
      this.fileList = [];
    },
    exportFunction(response, name, type) {
      const link = document.createElement("a");
      const blob = new Blob([response], { type });
      link.style.display = "none";
      link.href = URL.createObjectURL(blob);
      link.setAttribute("download", name, type);
      document.body.appendChild(link);
      link.click();
      document.body.removeChild(link);
    },
    // 预览
    handlePreview(file) {
      this.preview.url = "";
      if (file.fileType.indexOf("image") !== -1) {
        this.fileSuffix = "img";
      } else {
        this.fileSuffix = file.suffix;
      }
    },
    previewCancel() {
      this.preview.open = false;
      this.preview.url = "";
      if (this.fileSuffix == "mp3") {
        this.$refs.myAudio.pause();
      }
    },uploadRequest(file) { 
    axios.post(this.uploadFileUrl, file, {
      headers: {
        'Content-Type': 'multipart/form-data'
      },
      withCredentials: true,
    })
    .then(response => {
      this.handleUploadSuccess(response,file);
    })
    .catch(error => {
      this.handleUploadError(error);
    });
  }
  },
};
</script>

<style scoped lang="scss">
::v-deep .el-button--primary.is-plain {
  color: var(--select-selected-color);
  background: var(--table-content-bColor);
  border-color: var(--search-back);
}
::v-deep .el-button--primary.is-plain:hover,
::v-deep .el-button--primary.is-plain:focus {
  border-color: var(--select-selected-color);
  background-color: var(--select-selected-color);
  color: #fff !important;
}

.upload-file-uploader {
  margin-bottom: 5px;
}
.upload-file-list .el-upload-list__item {
  border: 1px solid #e4e7ed;
  line-height: 2;
  margin-bottom: 10px;
  position: relative;
}
.upload-file-list .ele-upload-list__item-content {
  display: flex;
  color: inherit;
  .link {
    flex: 1;
    display: block;
    margin-left: 2px;
    ::v-deep .el-icon-document {
      width: 50px;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
    }
  }
  .controls {
    display: flex;
  }
}
.ele-upload-list__item-content-action .el-link {
  margin-right: 10px;
}
::v-deep {
  .x-spreadsheet-table {
    width: auto !important;
  }
}
</style>
