<template>
  <div id="app">
    
    <div class="container">
      <h2>Your RAG</h2>
        <div class="showPage" style="height: 40pc; y:auto;">
          <h3>答案</h3>
          {{answer}}
          <hr>
          <h3>参考</h3>
          <table>
            <tr>
              <td style="color: crimson;">title</td> <td style="color: crimson;">页数</td>
            </tr>
            <tr v-for="(refer, index) in references" :key="index">
              <td>{{refer[0]}}</td>
              <td>{{refer[1] + 1}}</td>
            </tr>
        </table>
    </div>
      <div class="input-container">
    <el-input
    type="textarea"
    :autosize="{ minRows: 2, maxRows: 4}"
    placeholder="请输入问题"
    v-model="question">
  </el-input>
  
  <el-select v-model="broad_num" placeholder="请选择broad_num">
    <p>请选择宽泛程度</p>
    <el-option
      v-for="item in broad_nums"
      :key="item.value"
      :label="item.label"
      :value="item.value"
      :disabled="item.disabled">
    </el-option>
  </el-select>
  <el-button @click="query">查询</el-button>
   </div>
   <br><br>
  </div>
  <fileLoad></fileLoad>
  </div>
</template>

<script>
import fileLoad from './components/fileLoad.vue'
import axios from "axios";
axios.defaults.withCredentials = true;
export default {
  name: 'App',
  components: {
    fileLoad:fileLoad
  },
  data(){
      // Example request using fetch
      const a = this.$loading("正在初始化，请稍候...");
      axios.get('http://118.31.113.55:8083/init', { withCredentials: true })
      .then(response => {
        // Set cookie from response headers
        console.log("okokok")
        const setCookieHeader = response.headers['Set-Cookie'];
        console.log("当前 Cookie:", document.cookie,setCookieHeader); 
        // if (setCookieHeader) {
        //   document.cookie = setCookieHeader;
        // }
        console.log(response.data);
        a.close()
      })
      .catch(error => {
        a.close()
        alert(`初始化错误，请刷新哦，亲：${error}`)
      });
    return {
      question:"",
      broad_nums: [{
          value: '1',
          label: '1'
        }, {
          value: '2',
          label: '2',
        }, {
          value: '3',
          label: '3'
        }, {
          value: '4',
          label: '4'
        }, {
          value: '5',
          label: '5'
        }],
        broad_num:5,
        loader:null,
        answer:"",
        references:[]
    }
  },methods :{
    query(){
      this.loader = this.$loading("正在上传文件，请稍候...");
      axios.get(`http://118.31.113.55:8083/query?question=${this.question}&broad_num=${this.broad_num}`,
      {withCredentials: true}
    )
    .then(response => {
      console.log(response.data)
      this.answer = response.data.answer
      this.references = response.data.references
      this.loader.close()
    })
    .catch(error => {
      this.loader.close()
      alert(`请求错误：${error}`)
    })

    },
  }
}
</script>

